import { defineStore } from 'pinia'
import {v4 as uuid4} from 'uuid'

export default defineStore({
  id: 'UserStore',
  state: () => {
    return {
      UUID:"",
      Background_URL:"",
      Token:"",
      RefreshToken:""
    }  
  },
  
  getters: {},
  actions: {
    CreateUUID(){
      this.UUID = uuid4()
    },
    ChangeBackground(NewUrl:string){
      this.Background_URL = NewUrl
    },
    SetDefaultToken(token:string,refresh:string){
      this.$patch(state => {
        state.Token = token
        state.RefreshToken = refresh
      })
    },
    RefreshToken(){

    },
    ExitLogin(){
      this.$patch(state => {
        state.Token = ""
        state.RefreshToken = ""
      })
    }
  },

  persist: {
    enabled: true,
    strategies: [
      {
        storage: localStorage
      }
    ]
  }
})