import { createApp } from 'vue'
import App from './App.vue'
import router from './router'

//全局状态管理方案
import { createPinia } from "pinia"
import piniaPersist from 'pinia-plugin-persist'
const pinia = createPinia()
pinia.use(piniaPersist)
//图片懒加载
import vue_lazyload from 'vue-lazyload'
//Axios网络请求
import axios from '@/assets/js部分/axios初始化配置.js'
//animate.css使用
import 'animate.css';
//bootstrap 使用
import '@/assets/style.scss'
import "@popperjs/core";
import "bootstrap";
//自己的
import '@/assets/css部分/14-京东css初始化.css'
import '@/assets/js部分/刷新重载.js'

const app = createApp(App)
app.use(pinia)
app.use(router)
app.use(vue_lazyload)
app.provide('$axios',axios)
app.mount('#app')
