import { defineStore } from 'pinia'

export default defineStore({
  id: 'JumpTips',
  state: () => {
    return {
      Element: null as any,
      ShowTips:false,
      Errmsg:"",
      test:false,
      setting:{}
    }  
  },
  
  getters: {},
  actions: {
    JumpTipsShow(Errmsg:any,test:boolean=true,setting:object){
      /**
       * 1.结构赋值,正则,判断后端的返回错误信息是否提示, 当然这可以用 test 不运行,替换数字
       * 2.此方法用于显示 components/全局弹出消息 ,在 axios 响应拦截器那里使用
       */
      let {errmsg} = Errmsg
      //判断 
      if ( !errmsg || /^ok$/.test(errmsg) && test) return
      //这里代表有错或者单纯的想要提示
      errmsg = test ? errmsg.replace(/[0-9]/g, '') : errmsg
      this.$patch(state => {
        state.ShowTips = true
        state.Errmsg = errmsg
        state.test = !test
        state.setting = setting
      })
      this.Element = this.Element || document.querySelector('#jumptips>div')
      this.Element.setAttribute(`data-errtext`,this.Errmsg)
      this.Element.classList.remove('div_show')
      setTimeout(()=>this.Element.classList.add('div_show'))
    }
  }
})