import axios from 'axios'
import JumpTips from '../../store/JumpTips.ts'
import UserStore from '../../store/UserStore.ts'


const MyAxios = axios.create({
  method: 'get',
	baseURL: 'http://127.0.0.1:8000/', // 所有的请求地址前缀部分
	timeout: 50000, // 请求超时时间毫秒
	headers: {
		// 设置后端需要的传参类型
		'Content-Type': 'application/json',
	},
})

// 添加请求拦截器
MyAxios.interceptors.request.use(
	function (config) {
		const use_UserStore = UserStore()							//将 token 写入请求头
		use_UserStore.Token && (config.headers['Authorization'] = 'Bearer ' + use_UserStore.Token)
		config.url += "/"														//加一个 / 不然我写的难受
		return config
	},
	function (error) {
		// 对请求错误做些什么
		console.log(error)
		return Promise.reject(error)
	}
)

// 添加响应拦截器
MyAxios.interceptors.response.use(
	async function (response) {
		// 2xx 范围内的状态码都会触发该函数。
		
		const dataAxios = response.data							//只要了 data 全部内容在 response
		
		return dataAxios
	},
	function (error) {
		// 超出 2xx 范围的状态码都会触发该函数。
		const use_JumpTips = JumpTips()													//导入全局弹出消息
		use_JumpTips.JumpTipsShow(error.response.data)					//如果有错误,那么全局提示
		// 对响应错误做点什么
		console.log(error)
		return Promise.reject(error)
	}
)


export default MyAxios
