import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/:type?/:id?',
    name:"Root",
    component: () => import('@/views/首页与新闻页面切换/index.vue'),
  },
  {
    path: '/about',
    name: 'About',
    redirect: '/about/details',
    component: () => import('@/views/关于/index.vue'),
    children: [
      {
        path: 'details',
        name: 'Details',
        redirect: '/about/details/home',
        component: () => import('@/views/关于/个人详情页面/index.vue'),
        children: [
          {
            path: 'home',
            name: 'Home',
            component: () => import('@/views/关于/个人详情页面/首页/index.vue'),
          },
          {
            path: 'about',
            name: 'PersonAbout',
            component: () => import('@/views/关于/个人详情页面/关于/index.vue'),
          },
          {
            path: 'phone_number',
            name: 'PhoneNumber',
            component: () => import('@/views/关于/个人详情页面/还未完成制作/index.vue'),
          },
          {
            path: 'email',
            name: 'Email',
            component: () => import('@/views/关于/个人详情页面/还未完成制作/index.vue'),
          },
          {
            path: 'wechat',
            name: 'WeChat',
            component: () => import('@/views/关于/个人详情页面/还未完成制作/index.vue'),
          },
          {
            path: 'other',
            name: 'Other',
            component: () => import('@/views/关于/个人详情页面/还未完成制作/index.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/publish_article',
    name:"Article",
    component: () => import('@/views/用户发布文章界面/index.vue'),
  },
  {
    path: '/login',
    name:"Login",
    component: () => import('@/views/登陆注册页面/index.vue'),
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router

